// app/home/page.tsx - Ažurirana verzija
"use client";

import { useState, useEffect } from "react";
import { createClient } from "@/lib/supabase/client";
import { Feed } from "@/components/feed/feed";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { AlertCircle } from "lucide-react";
import { Skeleton } from "@/components/ui/skeleton";

export default function HomePage(): React.JSX.Element {
  const [user, setUser] = useState<any>(null);
  const [profile, setProfile] = useState<any>(null);
  const [followingUserIds, setFollowingUserIds] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [databaseError, setDatabaseError] = useState<string | null>(null);
  const [showFollowButtons, setShowFollowButtons] = useState(true); // Kontrola prikaza

  const supabase = createClient();

  useEffect(() => {
    fetchUserData();
  }, []);

  async function fetchUserData() {
    try {
      setIsLoading(true);
      
      // Dohvati trenutnog korisnika
      const { data: { user: currentUser } } = await supabase.auth.getUser();
      setUser(currentUser);

      if (currentUser) {
        // Dohvati profil
        const { data: userProfile } = await supabase
          .from("profiles")
          .select("*")
          .eq("id", currentUser.id)
          .maybeSingle();
        setProfile(userProfile);

        // Dohvati korisnike koje prati
        const { data: follows } = await supabase
          .from("follows")
          .select("following_id")
          .eq("follower_id", currentUser.id);
        
        const followingIds = follows?.map(follow => follow.following_id) || [];
        setFollowingUserIds([...followingIds, currentUser.id]); // Dodaj i sebe

        // Provjera da li tabela postoji
        const { error: profileError } = await supabase
          .from("profiles")
          .select("id")
          .limit(1);
        
        if (profileError?.message?.includes("Could not find the table")) {
          setDatabaseError("database_not_setup");
        }
      }
    } catch (error) {
      console.error("Error fetching user data:", error);
    } finally {
      setIsLoading(false);
    }
  }

  // Funkcija za ažuriranje liste pratilaca
  const handleFollowChange = (userId: string, isFollowing: boolean) => {
    if (isFollowing) {
      // Dodaj u listu pratilaca
      setFollowingUserIds(prev => [...prev, userId]);
    } else {
      // Ukloni iz liste pratilaca
      setFollowingUserIds(prev => prev.filter(id => id !== userId));
    }
  };

  if (isLoading) {
    return (
      <div className="mx-auto max-w-2xl p-4">
        <div className="mb-8">
          <Skeleton className="h-8 w-48 mb-2" />
          <Skeleton className="h-4 w-64" />
        </div>
        <div className="space-y-4">
          {[...Array(3)].map((_, i) => (
            <div key={i} className="p-6 border rounded-lg">
              <div className="flex items-center gap-3 mb-4">
                <Skeleton className="h-10 w-10 rounded-full" />
                <div>
                  <Skeleton className="h-4 w-32 mb-1" />
                  <Skeleton className="h-3 w-24" />
                </div>
                <Skeleton className="h-8 w-24 ml-auto" />
              </div>
              <Skeleton className="h-20 w-full" />
            </div>
          ))}
        </div>
      </div>
    );
  }

  if (databaseError === "database_not_setup") {
    return (
      <div className="mx-auto max-w-2xl p-4">
        <Alert>
          <AlertCircle className="h-4 w-4" />
          <AlertTitle>Database Setup Required</AlertTitle>
          <AlertDescription>
            The database tables haven't been created yet. Please run the SQL scripts in the{" "}
            <code className="rounded bg-muted px-1 py-0.5">scripts</code> folder to set up your database.
          </AlertDescription>
        </Alert>
      </div>
    );
  }

  const isAuthenticated = !!user;

  return (
    <div className="mx-auto max-w-2xl p-4">
      <div className="mb-8">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold">
              {isAuthenticated ? "Home" : "Explore Posts"}
            </h1>
            <p className="text-muted-foreground mt-2">
              {isAuthenticated 
                ? "Your personalized feed" 
                : "Sign in to see posts from people you follow"}
            </p>
          </div>
          
          {isAuthenticated && (
            <div className="flex items-center gap-2">
              <label className="flex items-center gap-2 text-sm">
                <input
                  type="checkbox"
                  checked={showFollowButtons}
                  onChange={(e) => setShowFollowButtons(e.target.checked)}
                  className="rounded"
                />
                Show follow buttons
              </label>
            </div>
          )}
        </div>
        
        {/* Statistika */}
        {isAuthenticated && profile && (
          <div className="mt-4 flex gap-4 text-sm">
            <div className="rounded-lg border px-3 py-2">
              <div className="font-medium">Following</div>
              <div className="text-lg font-bold">
                {followingUserIds.length - 1} {/* Oduzmi sebe */}
              </div>
            </div>
          </div>
        )}
      </div>

      <Feed 
        userId={user?.id}
        followingUserIds={followingUserIds}
        isAuthenticated={isAuthenticated}
        showFollowButton={showFollowButtons && isAuthenticated}
        onFollowChange={handleFollowChange} // Nova prop za ažuriranje
      />

      {/* Preporuke za praćenje */}
      {isAuthenticated && (
        <div className="mt-8 rounded-lg border p-4">
          <h3 className="font-semibold mb-3">Suggestions for you</h3>
          <UserSuggestions
            currentUserId={user.id}
            followingIds={followingUserIds}
            onFollowChange={handleFollowChange}
          />
        </div>
      )}
    </div>
  );
}

// Komponenta za preporuke korisnika
function UserSuggestions({ 
  currentUserId, 
  followingIds,
  onFollowChange 
}: { 
  currentUserId: string;
  followingIds: string[];
  onFollowChange: (userId: string, isFollowing: boolean) => void;
}) {
  const [suggestions, setSuggestions] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const supabase = createClient();

  useEffect(() => {
    fetchSuggestions();
  }, [currentUserId]);

  async function fetchSuggestions() {
    try {
      // Dohvati korisnike koje NE pratiš (isključujući sebe)
      const { data: allUsers } = await supabase
        .from("profiles")
        .select("id, username, display_name, avatar_url")
        .neq("id", currentUserId)
        .limit(5);

      if (allUsers) {
        // Filtriraj one koje već pratiš
        const notFollowing = allUsers.filter(
          user => !followingIds.includes(user.id)
        );
        setSuggestions(notFollowing);
      }
    } catch (error) {
      console.error("Error fetching suggestions:", error);
    } finally {
      setLoading(false);
    }
  }

  if (loading) {
    return <div className="text-muted-foreground">Loading suggestions...</div>;
  }

  if (suggestions.length === 0) {
    return (
      <div className="text-muted-foreground text-sm">
        No suggestions available. Try exploring posts to find people to follow.
      </div>
    );
  }

  return (
    <div className="space-y-3">
      {suggestions.map((user) => (
        <div key={user.id} className="flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="h-8 w-8 rounded-full bg-muted flex items-center justify-center">
              {user.avatar_url ? (
                <img 
                  src={user.avatar_url} 
                  alt={user.username}
                  className="h-full w-full rounded-full object-cover"
                />
              ) : (
                <span className="text-xs font-medium">
                  {user.display_name?.[0]?.toUpperCase() || user.username?.[0]?.toUpperCase()}
                </span>
              )}
            </div>
            <div>
              <div className="font-medium">{user.display_name}</div>
              <div className="text-sm text-muted-foreground">@{user.username}</div>
            </div>
          </div>
          <FollowButton
            followerId={currentUserId}
            followingId={user.id}
            isFollowing={false}
            onFollowChange={(isFollowing) => {
              onFollowChange(user.id, isFollowing);
              // Ukloni iz liste preporuka kada se zaprati
              if (isFollowing) {
                setSuggestions(prev => prev.filter(u => u.id !== user.id));
              }
            }}
            size="sm"
          />
        </div>
      ))}
    </div>
  );
}